<template>
    <transition name="slideu">
        <div class="container">
            <div class="heat-map-header slided">
                <p class="heat-map-title">{{$t(`lang.${title[charType]}`)}}</p>
                <div class="output-btn" :class="{'zh': $t('lang.btnName') == 'zh', 'en': $t('lang.btnName') == 'en'}" @click="screen()" v-show="!isIE()"></div>
                <a ref="downloadScreen" href="" download="" style="display: none;"></a>
            </div>
            <operation-pane @listenOperationType="operationType"></operation-pane>
            <heat-map ref="heatMap" v-if="charType===0 && dataFinished" @ctrlLoading="ctrlLoading"></heat-map>
            <histogram ref="histogram" v-if="charType===1"></histogram>
            <!-- <bottom-bar></bottom-bar> -->
            <div id="screenContainer">
                <div class="left">
                    <img class="logo" src="./img/logo.png" width="80" height="68" alt="">
                    <div class="year">{{screenYear}}</div>
                    <div class="type">{{screenArea}} {{screenPo}}</div>
                    <div class="chartstype">{{screenType}}</div>
                    <div class="tips">Web Info</div>
                </div>
                <div class="right" id="right"></div>
            </div>
            <loading v-show="showLoading"></loading>
        </div>
    </transition>
</template>

<script>
import {fetchExcel,fetchProvinceExcel} from '@/common/fetch/fetchExcelData'
import topBar from "@/components/top-bar";
import bottomBar from "@/components/bottom-bar";
import loading from "@/components/loading";
const operationPane = () => import('@/components/operation-pane')
// import heatMap from "@/components/heat-map"; 
const heatMap = () => import('@/components/heat-map')
// import histogram from '@/components/histogram';
const histogram = () => import('@/components/histogram')
import {mapMutations, mapState} from 'vuex'
import html2canvas from 'html2canvas';
export default {
    components: {
        operationPane,
        heatMap,
        topBar,
        bottomBar,
        histogram,
        loading
    },
    data() {
        return {
            charType:0,
            // title: ['', ''],
            title: ['rlt', 'zzt'],
            dataFinished: false,
            showLoading: true,
        };
    },
    created() {
        this.getExcelData();
    },
    methods:{
        ctrlLoading(flag) {
            this.showLoading = false;
        },
        isIE() {
            if (!!window.ActiveXObject || "ActiveXObject" in window){
                return true;
            }else{
                return false;
            }
        },
        operationType(data){
            this.charType = data
        },
        getExcelData() {
            fetchExcel('PM2d5').then(data => {
                this.savePM2d5(data)
                this.dataFinished = true;
            })
            fetchExcel('CO').then(data => {
                this.saveCO(data)
            })
            fetchExcel('NO2').then(data => {
                this.saveNO2(data)
            })
            fetchExcel('PM10').then(data => {
                this.savePM10(data)
            })
            fetchExcel('SO2').then(data => {
                this.saveSO2(data)
            })
            fetchExcel('O3').then(data => {
                this.saveO3(data)
            })
            //
            fetchProvinceExcel('p_PM2d5').then(data => {
                this.saveProvincePM2d5(data)
            })
            fetchProvinceExcel('p_CO').then(data => {
                this.saveProvinceCO(data)
            })
            fetchProvinceExcel('p_NO2').then(data => {
                this.saveProvinceNO2(data)
            })
            fetchProvinceExcel('p_PM10').then(data => {
                this.saveProvincePM10(data)
            })
            fetchProvinceExcel('p_SO2').then(data => {
                this.saveProvinceSO2(data)
            })
            fetchProvinceExcel('p_O3').then(data => {
                this.saveProvinceO3(data)
            })
            // ================================
            fetchExcel('74city').then(data => {
                this.save74city(data)
            })
            fetchExcel('hotCityName').then(data => {
                this.saveHotcity(data)
            })
            fetchExcel('cityData').then(data => {
                this.saveCityData(data)
            })
        },
        screen() {
            // charType:0: 1:
            let charts;
            if (this.charType === 0) {
                charts = this.$refs.heatMap.returnChartNode();//div
            } else if (this.charType === 1) {
                charts = this.$refs.histogram.returnChartNode();//div
            }
            let content_chart = document.getElementById("right")
            content_chart.innerHTML = ""
            content_chart.appendChild(charts);

            let content_html = document.getElementById("screenContainer");//div
            let width = content_html.offsetWidth;
            let height = content_html.offsetHeight;
            var opts = {
                allowTaint:true,//
                tainttest:true, //
                logging: false, //false
                width:width, //dom 
                height:height //dom 
            };
            html2canvas(content_html,opts).then( canvas  => {
                canvas.style.width = width+"px";
                canvas.style.height = height+"px";
                this.$refs.downloadScreen.setAttribute('href', canvas.toDataURL());
                this.$refs.downloadScreen.click();
                // console.log(canvas.toDataURL());
                let saveCharType = this.charType;
                this.charType = 88;
                // console.log(saveCharType);
                this.$nextTick(() => {
                    this.charType = saveCharType;
                })
            });
        },
        ...mapMutations({
            savePM2d5: 'savePM2d5',
            saveCO: 'saveCO',
            saveNO2: 'saveNO2',
            savePM10: 'savePM10',
            saveSO2: 'saveSO2',
            saveO3: 'saveO3',
            saveProvincePM2d5: 'saveProvincePM2d5',
            saveProvinceCO: 'saveProvinceCO',
            saveProvinceNO2: 'saveProvinceNO2',
            saveProvincePM10: 'saveProvincePM10',
            saveProvinceSO2: 'saveProvinceSO2',
            saveProvinceO3: 'saveProvinceO3',
            save74city: 'save74city',
            saveHotcity: 'saveHotcity',
            saveCityData: 'saveCityData',
        })
    },
    computed: {
        ...mapState({
            chartData: 'chartData',
        }),
        screenYear() {
            // const STOREDATA = ['PM2d5', 'NO2', 'PM10', 'O3', 'SO2', 'CO']
            let yearList = [2013, 2014, 2015, 2016, 2017, 2018]
            if (this.charType === 0) {
                return `${yearList[this.chartData.heatYear]}`;
            } else if (this.charType === 1) {
                let selectedYear = []
                this.chartData.barchartYear.forEach(index => {
                    selectedYear.push(`${yearList[index]}`);
                })
                return selectedYear.join(",")
            }
        },
        screenArea() {
            // const STOREDATA = ['PM2d5', 'NO2', 'PM10', 'O3', 'SO2', 'CO']
            let areaList = ['', '', '', '', '', ''];
            return areaList[this.chartData.area];
        },
        screenPo() {
            // const STOREDATA = ['PM2d5', 'NO2', 'PM10', 'O3', 'SO2', 'CO']
            if (this.chartData.tab === 1) {
                let pollutant = ['PM2d5', 'NO2', 'PM10', 'O3', 'SO2', 'CO']
                return pollutant[this.chartData.pollutant];
            } else {
                let po = [
                        { text: "", sname: "GT", type: 0 },
                        { text: "", sname: "SN", type: 1 },
                        { text: "", sname: "GL", type: 2 },
                        { text: "", sname: "HBC", type: 3 },
                        { text: "", sname: "QY", type: 4 }
                    ]
                let _this = this;
                for (let index in po) {
                    if(po[index].sname == _this.chartData.policy) {
                        return po[index].text;
                        break;
                    }
                }
                // return po[this.chartData.policy];
            }
        },
        screenType() {
            if (this.charType === 0) {
                return ''
            } else if (this.charType === 1) {
                return ''
            }
        }
    }
};
</script>


<style scoped lang="scss">
.container {
  position: fixed;
  top: 70px;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 9999;
  overflow-y: auto;
  background-color: #fff;
}
.heat-map-header{
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 10000;
    top: 70px;
    left: 0;
    height: 4rem;
    width: 100%;
    box-shadow: 0 0 4px 0 rgba(0,0,0,0.40);
    background-color: #fff;
    .heat-map-title{
        margin-left: 45px;
        padding-left: 25px;
        color:#32ADFE;
        font-size: 20px;
        border-left: 2px solid #32ADFE;
    }
    .output-btn{
        width: 114px;
        height: 30px;
        margin-right: 44px;
        background-repeat: no-repeat;
        background-size: contain;
        &.zh{
            background-image: url('./img/save-image.png');
        }
        &.en{
            background-image: url('./img/save-image_en.png');
        }
        user-select: none;
        cursor: pointer;
    }
}
#screenContainer{
    box-sizing: border-box;
    position: fixed;
    // width: 1150px;
    width: auto;
    height: 820px;
    padding: 30px;
    background-color: #fff;
    z-index: 10001;
    bottom: 5000px;
    left: 5000px;
    display: flex;
    overflow: hidden;
    .left{
        width: 180px;
        padding-right: 30px;
        color: #4A4A4A;
        .logo{
            margin-bottom: 10px;
        }
        .year{
            font-size: 12px;
            line-height: 18px;
            padding: 10px 0;
            border-top: 1px solid #333;
        }
        .type{
            font-size: 14px;
            line-height: 21px;
        }
        .tips{
            font-size: 14px;
            line-height: 26px;
            padding-top: 10px;
            font-family: 'Oswald-Medium';
            font-weight: bold;
            color: #9B9B9B;
        }
    }
    .right{
        flex: 1;
        overflow: hidden;
    }
    
}

</style>
